import "./index.css";
import React from "react";
import ReactDOM from "react-dom/client";
import restaurantData from "./restaurantData";
const HeaderLogo =
  "data:image/png;base64,";
const RatingIcon =
  "data:image/svg+xml; charset=utf-8;utf8;base64,";
const RupeeIcon =
  "data:image/svg+xml; charset=utf-8;utf8;base64,";

const Header = () => {
  return (
    <header className="header">
      <div className="logo-container">
        <img className="logo" src={HeaderLogo} alt="Logo" />
      </div>
      <div className="nav-items">
        <ul className="nav-list">
          <li className="nav-item">Home</li>
          <li className="nav-item">About</li>
          <li className="nav-item">Contact</li>
          <li className="nav-item">Cart</li>
        </ul>
      </div>
    </header>
  );
};

const styleCards = {
  padding: "10px",
  width: "200px",
};

const RestaurantCard = (props) => {
  const { restaurant } = props;

  const { name, cuisine, image, deliveryTime, rating, menu } = restaurant;

  const forTwo = menu[0].price;
  const priceForTwo = forTwo * 2;

  return (
    <div className="res-card" style={styleCards}>
      <img className="food-img" alt={`${name} dish`} src={image} />
      <h3>{name}</h3>
      <div className="TimeRating">
        <span style={{ display: "flex" }}>
          <img className="rIcon" alt="star" src={RatingIcon} /> {rating}
        </span>
        <span>{deliveryTime} min</span>
      </div>
      <h5 id="desc">{cuisine.join(", ")}</h5>
      <h4 style={{ display: "flex" }}>
        <img className="rIcon" alt="rupee" src={RupeeIcon} /> {priceForTwo} FOR
        TWO
      </h4>
    </div>
  );
};

const Body = () => {
  return (
    <div className="Body">
      <div className="Search">
        <input type="text" placeholder="Search restaurants..." />
      </div>
      <div className="res-container">
        {restaurantData.data.map((e) => (
          <RestaurantCard key={e.id} restaurant={e} />
        ))}
      </div>
    </div>
  );
};

const App = () => {
  return (
    <div className="app">
      <Header />
      <Body />
    </div>
  );
};

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(<App />);
